import React from 'react';
import styled from 'styled-components';

const AvatarStyled = styled.img`
  width: 25px;
  height: 25px;
  border-radius: 50%;
`

type AvatarProps = {
  src: string
  alt: string
}

export const Avatar: React.FC<AvatarProps> = ({src, alt}) =>
  <AvatarStyled src={src} alt={alt} />